.eventTitle{
    text-transform: uppercase;
    font-weight: 200;
    font-size: 1.8rem;
    line-height: 2rem;
    margin: 0;
    letter-spacing: 1px;
    word-spacing: 2px;
    color: rgba(0, 0, 0, .8);
}

.eventSlotTitle{
    text-transform: uppercase;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 1px;
    word-spacing: 2px;
    margin: 0;
    color: rgba(0, 0, 0, .6);
    border: 1px solid red;
    display: block;
}

.namePlate{
    background: rgba(0, 0, 0, .4);
    color: white;
    padding: 5px;
    text-align: left;
    position: relative;
    width: calc(100% - 10px);
    height: 30px;
    margin-top: -10px;
    border-radius: 0 0 7px 7px;
}
.namePlate p{
    margin: 0;
    color: rgba(255, 255, 255, .8);
}
.namePlate h6{
    margin: 0;
    font-weight: 400;
    color: rgba(255, 255, 255, .6);
}