html, body{
  padding: 0;
  margin:0;
  box-sizing: border-box;
  font-family: 'Fira Sans Extra Condensed', sans-serif;
}
.App {
  text-align: center;
  background: #F5F5DC;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center ;
  align-content: center ;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
p{
  margin: 0;
}

.mediaCast{
  aspect-ratio: 16 / 9;
  display: grid;
  gap: 10px;
  justify-content: center stretch;
  align-content: start;
  grid-template-rows: 10% 90%;
  border: 0px solid green;
  overflow: hidden;
  padding: 10px;
}

.branding{
  background: rgba(255, 255, 255, .1);
  display: grid;
  justify-content: center stretch;
  align-content:center;
  border-radius: 10px;
}
.joinCall{
  background: rgba(255, 255, 255, .1);
  display: grid;
  align-items: center;
  padding: 10px;
  width: 300px;
  margin: auto;
  border-radius: 10px;
}
.mainFrame{
  align-self: stretch;
  overflow: hidden;
  aspect-ratio: 16 / 9;
  border: 0px solid red;
  border-radius: 10px;
  background: rgba(20, 0, 0, 0);
  padding: 0px;
  display: grid;
  justify-content: stretch;
  align-items: stretch;
}
.designStream{
  /* align-self: stretch; */
  /* align-content: center; */
  display: grid;
  justify-content:  center stretch;
  border: 0px solid red;
  background: rgba(255, 255, 255, .1);  
  grid-template-rows: 4% 94%;
  max-height: 85vh;
  border-radius: 10px;
}
.videoStream{
  
  /* aspect-ratio: 16 / 9; */
  align-self: center;
  align-content: center;
  display: grid;
  /* grid-template-columns: 100%; */
  justify-content: center ;
  align-items: center stretch;
  border: 0px solid red;
  /* background: rgba(0, 0, 0, .2); */
  border-radius: 10px;
  overflow: hidden;
  padding: 10px;
  gap: 10px;
  max-height: 82vh;
}
.presentationStream{
  display: grid;
  grid-template-columns: 1fr 4fr;
  justify-content: center;
  align-items: center stretch;
  gap: 10px;
  padding: 10px;
}
.speakerFrame{
  aspect-ratio: 16 / 9;
   align-self: center;
  align-content: space-around;
  display: grid;
  justify-content: center stretch;
  align-items: space-around;
  border-radius: 10px;
  overflow: hidden;
  padding: 3px;
  background: rgba(255, 255, 255, .1);
  border: 0px solid blue;
  max-height: 78vh;
}

.Videoframe{
  /* width: calc(100% - 10px);
  max-height: calc(100% - 10px); */
  aspect-ratio: 16 / 9;
  /* place-self: center stretch; */
  /* overflow: hidden;
  background: rgba(255, 255, 255, .1);
  border-radius: 10px;
  padding: 10px;
  border: 1px solid green;
  display: flex;
  justify-content: center stretch;
  align-items: center; */
}
.designframe{
  overflow: hidden;
  border-radius: 10px;
  display: grid;
  justify-content: center;
  align-items: center;
}
.agVideo{
  /* background: rgba(255, 255, 255, 0.1);
  width: calc(100% - 6px);
  min-width: 100px;
  max-height: calc(100% - 6px); */
  
  aspect-ratio: 16 / 9;
  /* place-self: center stretch; */
  border-radius: 10px;
  border: 0px solid purple;
  /* border: 3px solid rgba(255, 255, 255, .3); */
  overflow: hidden;
}
.agVideo div{
  aspect-ratio: 16 / 9;
  background-color: rgba(255, 255, 255, 0);
}

input{
  background: rgba(255, 255, 255, .1);
  outline: none;
  border: none;
  padding: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, .3);
  width: calc(100% - 20px);
  margin: 0;
  margin-bottom: 10px;
}

.btn{
  background: rgba(0, 0, 0, .3);
  outline: none;
  border: none;
  padding: 10px;
  color: white;
  border-radius: 5px;
  transition-duration: .5s;
  cursor: pointer;
}

.btn:hover{
  background: rgba(0, 0, 0, .5);
}

.controls{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  margin-bottom: -10px;
  width: 150px;
  height: 50px;
  display: grid;
  grid-template-columns: repeat(3, 50px);
  justify-content: start;
  align-items: start;
  background: rgba(0, 0, 0, .3);
  border-radius: 10px;
  overflow: hidden;
}

.controlBtn{
  width: 50px;
  color: white;
  background: rgba(0, 0, 0, 0);
  transition-duration: .5s;
  align-self: stretch;
  display: grid;
  justify-content: center;
  align-items: center;
}

.controlBtn:hover{
  width: 50px;
  color: white;
  background: rgba(0, 0, 0, .7);
  color: DodgerBlue;
}

form{
  padding: 10px;
  width: calc(100% - 20px);
  display: grid;
}


.emptryVideoSlide{
  background: rgba(255, 255, 255, 0);
  width: 100px;
  min-width: calc(100% - 10px);
  max-height: calc(100% - 10px);
  aspect-ratio: 16 / 9;
  place-self: center stretch;
  border-radius: 10px;
  border: 3px solid rgba(255, 255, 255, .3);
}

#presentor{
  min-height: 100%;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
  align-self: stretch;
  background: rgba(255, 255, 255, .1);
  
}

#presentor div, #presentor div video{
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  align-self: stretch;
  background-color: rgba(255, 255, 255, 0);
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
